import { ActionTypes } from './types';
/* others */
export var FILTERS = {
    SHOW_ACTIVE: 'SHOW_ACTIVE',
    SHOW_ALL: 'SHOW_ALL',
    SHOW_COMPLETED: 'SHOW_COMPLETED'
};
/* action creatore  */
export var addTodo = function (text) {
    return {
        id: +Date.now(),
        text: text,
        type: ActionTypes.ADD_TODO
    };
};
export var deleteTodo = function (index) {
    return {
        index: index,
        type: ActionTypes.DELETE_TODO
    };
};
export var onEditTodo = function (id) {
    return {
        id: id,
        type: ActionTypes.ON_EDIT_TODO
    };
};
export var toggleTodo = function (id) {
    return {
        id: id,
        type: ActionTypes.TOGGLE_TODO
    };
};
export var setFilter = function (filter) {
    return {
        filter: filter,
        type: ActionTypes.SET_FILTER
    };
};
export var updateTodo = function (id, text) {
    return {
        id: id,
        text: text,
        type: ActionTypes.UPDATE_TODO
    };
};



